:root {
  --main-text-color: #000;
  --hover-text-color: #00acdc;
  --active-text-color: #00acdc;
  --active-line-color: #00acdc;
}

html {
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header-logo {
  width: 40px;
  height: 40px;
}

.nav-ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  color: var(--main-text-color);
  text-transform: uppercase;
  align-items: center;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-items:not(:last-child) {
  margin-right: 20px;
}

.nav-item:hover {
  color: var(--hover-text-color);
  transition: all .25s;
}

.is-active {
  color: var(--active-text-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-line-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

